import React from "react";
import ReactDOM from "react-dom";

import $ from "jquery";

import "./index.css";

var folder = "/images/";
$.ajax({
  url: folder,
  success: function (data) {
    $(data)
      .find("a")
      .attr("href", function (i, val) {
        if (val.match(/\.(jpe?g|png|gif)$/)) {
          $("body").append("<img src='" + folder + val + "'>");
        }
      });
  },
});
// print(images);

const img_list = [
  { image_url: "https://alexlamprecht.com/icons/bibtex.png", name: "Bib", size: "small", orientation: "portrait" },
  { image_url: "https://alexlamprecht.com/icons/pdf.png", name: "PDF", size: "small", orientation: "portrait" },
];

function App({ items }) {
  return (
    <React.Fragment>
      <Header></Header>
      <PictureGrid items={items}></PictureGrid>
    </React.Fragment>
  );
}

function PictureGrid({ items }) {
  return (
    <div className="pics-grid">
      {items.map((item) => (
        <Picture item={item} />
      ))}
    </div>
  );
}

function Picture({ item }) {
  return <img className="grid-pic" alt={item.name} src={item.image_url} />;
}

function Header() {
  return (
    <nav className="header">
      <ul className="nav-list">
        <li className="nav-elem">
          <a href="#">Home</a>
        </li>
        <li className="nav-elem">
          <a href="#">Gallery</a>
        </li>
        <li className="nav-elem">
          <a href="#">Contact</a>
        </li>
      </ul>
    </nav>
  );
}

ReactDOM.render(<App items={img_list} />, document.getElementById("root"));
